import { useState, useRef,useEffect } from "react";
import React from 'react'

function App() {
  const [result, setResult] = useState("");
  const inputRef = useRef(null);
  useEffect(() => inputRef.current.focus());
  const clickhandler=(e)=>{
    setResult(result.concat(e.target.name));
  }
  const clearhandler =() => {
    setResult('');
  }
  const backspacehandler = () => {
    setResult(result.slice(0,-1));
  }
  const resulthandler =() => {
    setResult(eval(result).toString());
  }



  return (
    <div className="main-container">
      <div className="display-field">
        <form>
          <input
            type="text"
            className="input-field"
            value={result}
            ref={inputRef}
          />
        </form>
      </div>
      <div className="buttons-container">
        <button className="btn" onClick={clearhandler}>Clear</button>
        <button className="btn" onClick={backspacehandler}>C</button>
        <button className="btn" onClick={resulthandler}>Result</button>
        <button name="." className="btn" onClick={clickhandler}>
          .
        </button>
        <button name="+" className="btn" onClick={clickhandler}>
          +
        </button>
        <button name="-" className="btn" onClick={clickhandler}>
          -
        </button>
        <button name="*" className="btn" onClick={clickhandler}>
          *
        </button>
        <button name="/" className="btn" onClick={clickhandler}>
          /
        </button>
        <button name="1" className="btn" onClick={clickhandler}>
          1
        </button>
        <button name="2" className="btn" onClick={clickhandler}>
          2
        </button>
        <button name="3" className="btn" onClick={clickhandler}>
          4
        </button>
        <button name="5" className="btn" onClick={clickhandler}>
          5
        </button>
        <button name="6" className="btn" onClick={clickhandler}>
          6
        </button>
        <button name="7" className="btn" onClick={clickhandler}>
          7
        </button>
        <button name="8" className="btn" onClick={clickhandler}>
          8
        </button>
        <button name="9" className="btn" onClick={clickhandler}>
          9
        </button>
        <button name="0" className="btn" onClick={clickhandler}>
          0
        </button>
      </div>
    </div>
  );
}

export default App;
